<script lang="ts">
import { h } from 'vue';
import { useRoute, useRouter } from 'vue-router';
export default {
    setup() {
        const router = useRouter();
        const route = useRoute();
        router.replace(route.fullPath.replace(/^\/redirect/, ''));
    },
    render() {
        return h('div');
    },
};
</script>
